import React, { useState } from 'react';
import injectSheet from 'react-jss';
import Button from '../customs/button';
import './animate.css';
import Intro from './intro';
//import PageA from './surveyPageA';
import SurveyGame from './surveyGame';
import GameResult from './gameResult';

function Home() {
    const [page, setPage] = useState("Intro");
    const [result, setResult] = useState({bests: [], worsts: []});
    // const [page, setPage] = useState("GameResult");
    // const testBest = [];
    // testBest["r"] = 3;
    // testBest["i"] = 3;
    // const testWorst = [];
    // testWorst["s"] = 3;
    // testWorst["c"] = 3;
    // testWorst["a"] = 3;
    // testWorst["e"] = 3;
    // const [result, setResult] = useState({bests: testBest, worsts: testWorst});
    const components = {
        "Intro": <Intro onNext={() => setPage("Game")} />,
        "Game": <SurveyGame onNext={
            (bests, worsts) => 
            {
                setResult({bests, worsts});
                setPage("GameResult");
            }} />,
        "GameResult": <GameResult result={result}/>
        //"Result": <GameResult onNext={() => setPage("Intro")} />,
    }

    console.log(result);
    // Declare a new state variable, which we'll call "count"
    return (
        <React.Fragment>
            {
                components[page]
            }
        </React.Fragment>
    );
}

export default Home;