import React, { useState, useContext } from 'react';
import injectSheet from 'react-jss';

import { FacebookShareButton, FacebookIcon, TwitterShareButton, TwitterIcon, LineShareButton, LineIcon } from 'react-share';
import {Helmet} from "react-helmet";
import {withRouter, Redirect} from 'react-router-dom';

import {getUrl, getTitle, getImage, getDescription} from '../../HelmetMeta';


function GameResult(props) {
    const Title = "God";
    //const shareUrl = !process.env.PUBLIC_URL ? "https://kingkong.com.tw" : process.env.PUBLIC_URL;
    return (
        <div>
            <FacebookShareButton
                url={getUrl("it")}>
                <FacebookIcon
                size={32}
                round />
            </FacebookShareButton>
            <TwitterShareButton
                url={getUrl("")}
                title={"情境測驗!"}>
                <TwitterIcon
                size={32}
                round />
            </TwitterShareButton>
            <LineShareButton
                url={getUrl("")}
                title={"情境測驗!"}>
                <LineIcon 
                    size={32}
                round />
            </LineShareButton>
        </div>
    );
}

export default withRouter(props => <GameResult {...props}/>);